<template>
  <div
    class="my-table"
    id="my-table"
    :style="{'height':`${innerHeight}px`}"
  >
    <div
      class="table"
      :style="{'width':`${tdsWidth}px`}"
    >
      <div class="tr">
        <div class="head">
          <!-- 类型选择 -->
          <div class="th">
            <select
              @change="choose($event)"
              class="type_box"
            >
              <option
                v-for="(name,index) in chooseList"
                :key="index"
                :value="name.dictLabel"
              >{{ name.dictLabel }}</option>
            </select>
          </div>
          <!-- 时间 -->
          <div
            class="tds"
            id="head-tds"
          >
            <div
              class="td c_time"
              v-for="(statesItem, index) in getDates"
              :key="`${index}_states`"
            >
              <p>{{statesItem.substr(5,5)}}</p>
              <p v-html="setStyle_1(week[new Date(statesItem.replace(/-/g, '/')).getDay()])"></p>
            </div>
          </div>
        </div>
      </div>
      <div
        class="tr"
        style="margin-bottom:10px;border-top:1px solid #4B4B4B;"
        v-for="(value , name) in tableDate"
        :key="name"
      >
        <!-- 基础房型 -->
        <div
          class="body"
          id="myScoll"
        >
          <div
            class="th center_left"
            @click="btnChange(value)"
          >
            <!-- <div class="th center_left"> -->
            <div v-if="leftObj">
              <p>{{ leftObj[name] }}</p>
              <p style="color:#888;">共{{value.list[0].roomCount}}间</p>
            </div>
            <span>
              <img
                v-if="!value.value_statu"
                style="display:inline-block;width:20px;height:20px;"
                src="../../assets/right.png"
                alt
              />
              <img
                v-else
                style="display:inline-block;width:20px;height:20px;"
                src="../../assets/bottom.png"
                alt
              />
            </span>
          </div>
          <div
            v-for="(item,index) in value.list[0].roomStates"
            :key="index"
            class="td c_scale"
          >{{ item.roomCount-item.roomBook }}间</div>
        </div>
        <!-- 产品房型 -->
        <div
          class="children"
          v-if="value.value_statu"
        >
          <!-- <div class="children"> -->
          <div
            class="body"
            v-for="(childrenItem, name) in value.list"
            :key="name"
          >
            <div class="th c_pro">{{childrenItem.scaleName}}</div>
            <div
              class="tds"
              v-for="(item, index_0) in childrenItem.roomStates"
              :key="index_0"
            >
              <div
                class="td line_td"
                @click="btnGet(value,name,index_0,item,childrenItem.scaleName)"
              >
                <div
                  style="color:#fb9e58;"
                  v-if="item.maxSaleCount == 0"
                >满房</div>
                <div v-else>
                  <span
                    v-if="item.maxSaleCount-item.virtualRoomBook>item.roomCount-item.roomBook?item.roomCount-item.roomBook:item.maxSaleCount-item.virtualRoomBook != 0"
                  >
                    {{ item.maxSaleCount-item.virtualRoomBook>item.roomCount-item.roomBook?item.roomCount-item.roomBook:item.maxSaleCount-item.virtualRoomBook }}间
                  </span>
                  <span
                    style="color:#fb9e58;"
                    v-else
                  >满房</span>
                </div>
                <!-- <div style="color:#fb9e58;" v-else>满房</div> -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 修改房量 -->
    <mt-popup
      v-model="edit_show"
      position="center"
      style="width:90%;border-radius:10px;"
    >
      <div style="padding-left:20px;text-align:left;line-height:30px;">
        <p style="font-weight:bold;font-size:16px;margin-top:5px;">修改房量</p>
        <!-- 房型名称 -->
        <div style="-color:#888;-display:flex;align-items: center;">
          <p>
            {{ choosedName }}&emsp;{{day}}
            <br />
          </p>
        </div>
      </div>
      <!-- 房态设置 -->
      <div style="display:flex;align-items:center;text-align:left;padding-left:20px;white-space:nowrap;">
        <p>房态设置</p>
        <p style="width:78%;">
          <mt-radio
            style="display:flex;border:none;"
            v-model="statu"
            :options="states"
          ></mt-radio>
        </p>
      </div>
      <!-- 房量设置 -->
      <div
        v-if="statu == 0"
        style="display:flex;align-items:center;text-align:left;padding-left:20px;white-space:nowrap;"
      >
        <p>房量设置</p>
        <p style="width:78%;">
          <mt-radio
            style="display:flex;border:none;"
            v-model="value"
            :options="options"
          ></mt-radio>
        </p>
      </div>
      <!-- 限量房量-false -->
      <div
        v-if="value == 1 && statu == 0"
        style="display:flex;padding-left:20px;justify-content:space-between;line-height:30px;margin-bottom:10px;"
      >
        <p style="position:relative;">
          剩余房量
          <span style="position:absolute;top:14px;left:0;font-size:12px;">(0为锁房)</span>
        </p>
        {{ allCount }}
        <div style="display:flex;padding-right:10px;width:60%;">
          <button
            style="width:25%;border:1px solid #cecece;"
            v-show="reNum_show"
            id="btn_reduce"
            @click="reduce_num"
          >-</button>
          <P style="width:35%;border:1px solid #cecece;">{{ currentNum }}</P>
          <button
            style="width:25%;border:1px solid #cecece;"
            v-show="addNum_show"
            id="btn_add"
            @click="add_num"
          >+</button>
        </div>
      </div>
      <!-- 按钮 -->
      <div class="btn">
        <p
          style="width:50%;border:1px solid #cecece;border-bottom-left-radius:10px;"
          @click="btn_cancel"
        >取消</p>
        <p
          style="width:50%;border:1px solid #cecece;border-bottom-right-radius:10px;color:#fb9e58;"
          @click="btn_ok"
        >确定</p>
      </div>
    </mt-popup>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessRoomscaleStateEdit, GetBusinessRoomstateBookByhotel } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            name: '日租房',
            tableDate: {},
            clientWidth: '',
            infoAll: [],
            chooseList: [],
            week: ['日', '一', '二', '三', '四', '五', '六'],
            getDates: [],
            tdsWidth: 0,
            itemValue: {},
            leftObj: {},
            edit_show: false,
            choosedName: '',
            value: '0',
            options: [
                {
                    label: '无限量',
                    value: '0'
                },
                {
                    label: '限量',
                    value: '1'
                }
            ],
            statu: '0',
            states: [
                {
                    label: '开房',
                    value: '0'
                },
                {
                    label: '关房',
                    value: '1'
                }
            ],
            day: '',
            allCount: '',
            currentNum: '0',
            reNum_show: true,
            addNum_show: true,
            innerHeight: '',
            pageNum: 1,
            pageSize: 10,
            scrollLeft: 0
        };
    },
    computed: {
        setStyle_1(type) {
            return function (type) {
                if (type == '五' || type == '六') {
                    return `<span style="color:#60;font-size:10px;color:#fb9e58;">周${type}</span>`;
                } else {
                    return `<span style="color:#60;font-size:10px;">周${type}</span>`;
                }
            };
        }
    },
    created() {
        this.$store.commit('setHeader', '房态管理');
        this.getmore();
        this.innerHeight = window.innerHeight - 50;
    },
    mounted() {
        window.addEventListener(
            'scroll',
            () => {
                let scrollLeft = document.getElementById('my-table').scrollLeft;
                let clientWidth = document.getElementById('my-table').clientWidth;
                let scrollWidth = document.getElementById('my-table').scrollWidth;
                if (parseInt(scrollWidth - scrollLeft) == clientWidth) {
                    this.pageNum++;
                    this.getState();
                }
            },
            true
        );
    },
    methods: {
        // 房型类型接口
        getmore() {
            GetSystemDictDataByType({ dictType: 'business_virtual_room_scale_type' }).done((res) => {
                this.chooseList = res.data;
            });
            this.getState();
        },
        // 房型类型选择
        choose(e) {
            for (let i = 0; i < this.chooseList.length; i++) {
                if (e.target.value == this.chooseList[i].dictLabel) {
                    this.scaleType = this.chooseList[i].dictValue;
                    this.pageNum = 1;
                    this.tableDate = {};
                    this.getDates = [];
                }
            }
            this.getState();
        },
        getState() {
            let data = {
                scaleType: this.scaleType == undefined ? 1 : this.scaleType,
                pageNum: this.pageNum,
                pageSize: this.pageSize
            };
            GetBusinessRoomstateBookByhotel(this.$route.query.hotelId, data).done((res) => {
                if (!res.data) {
                    return;
                }
                this.infoAll.concat(res.data);
                this.infoAll = res.data;
                let tableDate = this.tableDate;
                this.leftObj = {};
                for (let i = 0; i < this.infoAll.length; i++) {
                    this.leftObj[this.infoAll[i].roomScaleId] = this.infoAll[i].roomScale.scaleName;
                }
                this.infoAll.forEach((item, index) => {
                    // 循环获取时间
                    item.roomStates.forEach((item) => {
                        if (this.getDates.indexOf(item.day) ==
 -1) {
                            this.getDates.push(item.day);
                        }
                    });
                    // 判断基础房型key是否存在，如果不存在表示第一次加载数据，如果存在表示分页执行。
                    if (tableDate[item.roomScaleId]) {
                        // 判断查询第一页时，执行基础房型赋值产品房型
                        if (this.pageNum ==
 1) {
                            tableDate[item.roomScaleId].list.push({
                                scaleName: item.scaleName,
                                roomStates: item.roomStates,
                                roomCount: item.roomStates[0].roomCount,
                                roomBook: item.roomStates[0].roomBook,
                                roomScaleId: item.id
                            });
                        } else {
                            // 遍历基础房型下产品房型并将新房间数合并到基础房型。
                            tableDate[item.roomScaleId].list.forEach((scaleItem) => {
                                // 判断产品房型对应的名称是否相同，如果相同执行数组合并。
                                if (scaleItem.scaleName ==
 item.scaleName) {
                                    scaleItem.roomStates = scaleItem.roomStates.concat(item.roomStates);
                                }
                            });
                        }
                    } else {
                        for (let i = 0; i < item.roomStates.length; i++) {}
                        tableDate[item.roomScaleId] = {
                            list: [],
                            value_statu: true
                        };
                        tableDate[item.roomScaleId].list.push({
                            scaleName: item.scaleName,
                            roomStates: item.roomStates,
                            roomCount: item.roomStates[0].roomCount,
                            roomBook: item.roomStates[0].roomBook,
                            roomScaleId: item.id
                        });
                    }
                });
                // 等下次渲染执行获取元素宽度
                this.$nextTick(function () {
                    let dom = document.getElementById('head-tds');
                    this.tdsWidth = dom.clientWidth || dom.offsetWidth;
                });
            });
        },
        // 展示产品房型
        btnChange(value) {
            this.value = value;
            value.value_statu = !value.value_statu;
        },
        // 弹出框
        btnGet(children, index, index_0, item, name) {
            this.item_virtualRoomBook = item.virtualRoomBook;
            this.roomScale = children.list.filter((item) => item.scaleName == name);
            let sum = 0;
            children.list.forEach((element) => {
                if (element.roomStates[index_0].maxSaleCount == 0) {
                    element.roomStates[index_0].maxSaleCount = element.roomStates[index_0].virtualRoomBook = 0;
                }
                sum += element.roomStates[index_0].maxSaleCount - element.roomStates[index_0].virtualRoomBook;
            });
            this.totleSum = sum;
            this.roomScaleId = this.roomScale[0].roomScaleId;
            this.day = item.day;
            if (this.totleSum <= item.maxSaleCount - item.virtualRoomBook) {
                Toast({
                    message: '暂不能修改',
                    duration: 900
                });
                return false;
            }
            if (children.list.length == 1) {
                Toast({
                    message: '暂不能修改',
                    duration: 900
                });
                return false;
            } else if (children.list.length > 1) {
                this.index = index;
                this.index_0 = index_0;
                this.currentNum = item.maxSaleCount - item.virtualRoomBook > item.roomCount - item.roomBook ? item.roomCount - item.roomBook : item.maxSaleCount - item.virtualRoomBook;
                this.virtualRoomBook = item.roomBook;
                let other = children.list.filter((item) => item.scaleName != name);
                this.item = item;
                if (other.length == 1) {
                    let other_child = other[0].roomStates[this.index_0];
                    this.other_child = other_child.maxSaleCount - other_child.virtualRoomBook > other_child.roomCount - other_child.roomBook ? other_child.roomCount - other_child.roomBook : other_child.maxSaleCount - other_child.virtualRoomBook;
                    if (this.currentNum + this.other_child >= this.item.roomCount - this.item.roomBook) {
                        this.max = this.item.roomCount - this.item.roomBook;
                        this.min = this.item.roomCount - this.item.roomBook - this.other_child;
                    }
                } else {
                    this.other_child_0 = 0;
                    this.other_child_1 = 0;
                    this.other_child_2 = 0;
                    for (var i = 0; i < other.length; i++) {
                        if (i == 0) {
                            let other_child_0 = other[i].roomStates[this.index_0];
                            this.other_child_0 =
                                other_child_0.maxSaleCount - other_child_0.virtualRoomBook > other_child_0.roomCount - other_child_0.roomBook
                                    ? other_child_0.roomCount - other_child_0.roomBook
                                    : other_child_0.maxSaleCount - other_child_0.virtualRoomBook;
                        }
                        if (i == 1) {
                            let other_child_1 = other[i].roomStates[this.index_0];
                            this.other_child_1 =
                                other_child_1.maxSaleCount - other_child_1.virtualRoomBook > other_child_1.roomCount - other_child_1.roomBook
                                    ? other_child_1.roomCount - other_child_1.roomBook
                                    : other_child_1.maxSaleCount - other_child_1.virtualRoomBook;
                        }
                        if (i == 2) {
                            let other_child_2 = other[i].roomStates[this.index_0];
                            this.other_child_2 =
                                other_child_2.maxSaleCount - other_child_2.virtualRoomBook > other_child_2.roomCount - other_child_2.roomBook
                                    ? other_child_2.roomCount - other_child_2.roomBook
                                    : other_child_2.maxSaleCount - other_child_2.virtualRoomBook;
                        }
                        if (this.currentNum + this.other_child_0 + this.other_child_1 + this.other_child_2 >= this.item.roomCount - this.item.roomBook) {
                            this.max = this.item.roomCount - this.item.roomBook;
                            this.min = this.item.roomCount - this.item.roomBook - (this.other_child_0 + this.other_child_1 + this.other_child_2);
                        }
                    }
                }
                this.min = this.min < 0 ? 0 : this.min;
                this.max = this.max;
            }
            this.edit_show = true;
            this.choosedName = name;
            this.allCount = item.roomCount - item.roomBook;
        },
        reduce_num() {
            if (this.min == this.currentNum) {
                document.getElementById('btn_reduce').disabled = true;
                Toast({
                    message: `最少设置${this.min}间`,
                    duration: 900
                });
                this.addNum_show = true;
                return false;
            }
            this.currentNum--;
            this.addNum_show = true;
            if (this.min == this.currentNum) {
                document.getElementById('btn_reduce').disabled = true;
            }
            if (this.currentNum < this.max) {
                document.getElementById('btn_add').disabled = false;
            }
        },
        add_num() {
            if (this.max == this.currentNum) {
                document.getElementById('btn_add').disabled = true;
                Toast({
                    message: `最多设置${this.max}间`,
                    duration: 900
                });
                this.addNum_show = true;
                return false;
            }
            this.currentNum++;
            if (this.currentNum == this.max) {
                document.getElementById('btn_add').disabled = true;
            }
            if (this.min < this.currentNum) {
                document.getElementById('btn_reduce').disabled = false;
            }
        },
        btn_cancel() {
            this.statu = '0';
            this.value = '0';
            this.edit_show = false;
        },
        // 确定
        btn_ok() {
            let data = '';
            if (this.statu == 1) {
                // 关房
                if (this.min != 0) {
                    Toast({
                        message: `至少设置${this.min}间`,
                        duration: 900
                    });
                    return false;
                } else {
                    data = {
                        roomScaleId: this.roomScaleId,
                        maxSaleRoom: 0,
                        stateDate: this.day
                    };
                }
            } else {
                // 开房
                data = {
                    roomScaleId: this.roomScaleId,
                    maxSaleRoom: this.value == 0 ? '-1' : Number(this.currentNum) + Number(this.item_virtualRoomBook),
                    stateDate: this.day
                };
            }
            PostBusinessRoomscaleStateEdit(data).done((res) => {
                location.reload();
                this.getState();
            });
            this.statu = '0';
            this.value = '0';
            this.edit_show = false;
        }
    }
};
</script>

<style scoped>
.my-table {
    overflow: auto;
    font-size: 14px;
}
.table > .tr:nth-child(1) {
    position: sticky;
    top: 0;
    z-index: 1;
    color: #5f5f5f;
    background-color: #f2f2f2;
}
.table .th {
    position: sticky;
    left: 0;
    background-color: #f2f2f2;
}
.table .th,
.table .td {
    border: 0.5px solid #e8e8e8;
    width: 100px;
    box-sizing: border-box;
}
.table .td {
    width: 50px;
}
.table .body,
.table .head {
    display: -webkit-box;
    width: 100%;
}
.tds {
    display: flex;
}
.td {
    background-color: #fff;
    color: #02bc6c;
}
.line_td {
    line-height: 50px;
}
/* 我的 */
.type_box {
    background-color: #f2f2f2;
    margin-top: 8px;
    outline: medium;
    border: none;
}
.center_left {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.center_left > div {
    text-align: left;
    padding-left: 6px;
    margin: 10px 0;
}
.center_left > div > p:nth-child(1) {
    color: #313443;
    font-weight: bold;
}
.children {
    line-height: 30px;
}
.children > div > .c_pro {
    text-align: left;
    padding: 10px 0;
    padding-left: 6px;
}
.c_pro {
    color: #888;
}
.c_scale {
    color: #313443;
    display: flex;
    line-height: 50px;
    justify-content: center;
}
.c_time {
    color: #5f5f5f;
    padding: 12px 0;
}
.btn {
    display: flex;
    justify-content: space-between;
    line-height: 40px;
}
button {
    background-color: #fff;
    line-height: 30px;
    border: none;
    color: #fb9e58;
}
</style>



